<template>
  <div class="squareShow" v-if="type == 0">
    <div class="horizontalParent container-fluid" v-if="direction == 0">
      <div class="row">
        <template v-for="item in datas" :key="item.id">
          <div class="img_word_item col col-xl-2 col-lg-3 col-md-4 col-6">
            <div :class="shadow ? 'img_shadow' : ''">
              <router-link to="/GameDetails">
                <div class="imgDiv">
                  <img :src="item.thumbnail" />
                  <div class="icon" v-if="icon">
                    <img
                      v-if="item.platform.indexOf('android') > -1"
                      class="android"
                      src="@/assets/Android-icon.svg"
                    />
                    <img
                      v-if="item.platform.indexOf('ios') > -1"
                      class="ios"
                      src="@/assets/Ios-icon.svg"
                    />
                  </div>
                </div>
              </router-link>
              <h6 v-if="caption != 'none'">{{ item.title }}</h6>
              <p v-if="detail">{{ item.excerpt }}</p>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div class="verticalParent container-fluid" v-if="direction == 1">
      <div class="row">
        <template v-for="item in datas" :key="item.id">
          <div class="img_word_item col col-12">
            <div :class="shadow ? 'img_shadow' : ''">
              <router-link to="/GameDetails">
                <div class="imgDiv">
                  <img :src="item.thumbnail" />
                  <div class="icon" v-if="icon">
                    <img
                      v-if="item.platform.indexOf('android') > -1"
                      class="android"
                      src="@/assets/Android-icon.svg"
                    />
                    <img
                      v-if="item.platform.indexOf('ios') > -1"
                      class="ios"
                      src="@/assets/Ios-icon.svg"
                    />
                  </div></div
              ></router-link>
              <h6 v-if="caption != 'none'">{{ item.title }}</h6>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>

  <div class="rectShow" v-if="type == 1">
    <div class="horizontalParent container-fluid" v-if="direction == 0">
      <div class="row">
        <template v-for="item in datas" :key="item.id">
          <div class="img_word_item col col-xl-4 col-12">
            <div :class="shadow ? 'img_shadow' : ''">
              <router-link to="/GameDetails">
                <div class="imgDiv">
                  <img :src="item.banner" />
                  <div class="icon" v-if="icon">
                    <img
                      v-if="item.platform.indexOf('android') > -1"
                      class="android"
                      src="@/assets/Android-icon.svg"
                    />
                    <img
                      v-if="item.platform.indexOf('ios') > -1"
                      class="ios"
                      src="@/assets/Ios-icon.svg"
                    />
                  </div></div
              ></router-link>
              <div class="word_btn">
                <h6 v-if="caption != 'none'" :style="{ textAlign: caption }">
                  {{ item.title }}
                </h6>
                <div v-if="timer" class="timerBtn">
                  {{ item.time ?? "00:10:22:33" }}
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div class="verticalParent container-fluid" v-if="direction == 1">
      <div class="row">
        <template v-for="item in datas" :key="item.id">
          <div class="img_word_item col col-12">
            <div
              :class="
                (shadow ? 'img_shadow' : '') +
                ' ' +
                (detail ? 'img_hasDetail' : '')
              "
            >
              <router-link to="/GameDetails">
                <div class="imgDiv">
                  <img :src="item.banner" />
                  <div class="icon" v-if="icon && !detail">
                    <img
                      v-if="item.platform.indexOf('android') > -1"
                      class="android"
                      src="@/assets/Android-icon.svg"
                    />
                    <img
                      v-if="item.platform.indexOf('ios') > -1"
                      class="ios"
                      src="@/assets/Ios-icon.svg"
                    />
                  </div></div
              ></router-link>
              <div class="word_btn" v-if="!detail">
                <h6 v-if="caption != 'none'" :style="{ textAlign: caption }">
                  {{ item.title }}
                </h6>
                <div v-if="timer" class="timerBtn">
                  {{ item.time ?? "00:10:22:33" }}
                </div>
              </div>
              <div class="word_btn_detail" v-if="detail">
                <h6 v-if="caption != 'none'" :style="{ textAlign: caption }">
                  {{ item.title }}
                </h6>
                <p v-if="detail">{{ item.excerpt }}</p>
                <div class="icon" v-if="icon && detail">
                  <img
                    v-if="item.platform.indexOf('android') > -1"
                    class="android"
                    src="@/assets/Android-icon.svg"
                  />
                  <img
                    v-if="item.platform.indexOf('ios') > -1"
                    class="ios"
                    src="@/assets/Ios-icon.svg"
                  />
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: { default: 0, type: Number }, //0:正方形,1:長方形
    icon: { default: false, type: Boolean }, //顯示裝置
    timer: { default: false, type: Boolean }, //倒數時間
    shadow: { default: false, type: Boolean }, //外框陰影
    detail: { default: false, type: Boolean }, //內容顯示
    caption: { default: "center", type: String }, //標題
    direction: { default: 0, type: Number }, //0:橫,1:縱
    width: { default: 6, type: Number }, //寬幾個
    datas: {
      default: function () {
        var ary = [
          {
            id: 1,
            title: "遊戲1",
            excerpt: "遊戲說明1",
            thumbnail:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/1_2559960a03.gif",
            banner:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/11_95c91d93c3.jpg",
            platform: ["pc", "web", "android", "ios"],
          },
        ];
        return ary;
      },
      type: Object,
    }, //array [{url:xxx}]
  },
  computed: {
    // getCaptionPos() {
    //   if (this.caption == "left")
    //     return {
    //       "text-align": this.caption,
    //     };
    // },
  },
};
</script>

<style scoped lang="scss">
.container-fluid {
  padding: 0;
}
.horizontalParent {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  align-items: center;
  &:has(p) {
    h6 {
      margin: 8px 0;
    }
  }
}
h6 {
  font-weight: bold;
  color: #727171;
  font-size: 1.2em;
}
.verticalParent {
}
.squareShow img {
  max-width: 195px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 16px;
}
.rectShow img {
  width: 100%;
  box-sizing: border-box;
  border-radius: 6px;
}
.rectShow .horizontalParent img {
  border-radius: 20px 20px 0 0;
}
.rectShow {
  height: 100%;
}
.timerBtn {
  background-color: #fa79fe;
  border: none;
  color: white;
  padding: 3px 0;
  border-radius: 15px;
  font-weight: bold;
  font-size: 1.2em;
}
.img_word_item {
  margin-top: 5px;
  margin-bottom: 5px;
  p {
    color: #9fa0a0;
    font-weight: 600;
    text-align: left;
  }
}
.img_shadow {
  background-color: #f7f6ef;
  border-radius: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  box-shadow: 3px 3px 5px 0px #d3d1d0;
}
.imgDiv {
  position: relative;
  .icon {
    width: 30px;
    position: absolute;
    z-index: 9;
    bottom: 0;
    right: 0;
    margin-bottom: 4%;
    margin-right: 6%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
.word_btn {
  padding: 15px 20px;
}
.img_hasDetail {
  background-color: #e6e6e6;
  border-radius: 6px 20px 20px 6px;
  box-shadow: 3px 3px 5px 0px #d3d1d0;
  margin-bottom: 15px;
  display: flex;
  position: relative;
  img {
    width: 100%;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
  }
  .word_btn_detail {
    width: 70%;
    padding: 20px 35px;
  }
  .icon {
    width: 35px;
    position: absolute;
    z-index: 9;
    bottom: 0;
    right: 0;
    margin: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
@media (max-width: 992px) {
  .img_hasDetail {
    border-radius: 6px 6px 20px 20px;
    flex-direction: column;
    img {
      border-radius: 6px;
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
    }
    .word_btn_detail {
      width: 100%;
    }
  }
}
</style>
